.wrapper {
  margin: .8rem;
  padding: .8rem;
  display: flex;
  flex-flow: row nowrap;
  border: .1rem solid gray;
  border-radius: 1.6rem;
}
.image {
  width: 5rem;
  height: 5rem;
  border-radius: 2.5rem;
}
.contentContainer {
  margin-left: .8rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
.nameText {
  color: #000;
  font-size: 1.6rem;
  font-weight: 700;
}
.commentText {
  color: #000;
  font-size: 1.6rem;
}